    color: #ea2e2b;
}

h1, .h1, .kopa-dropcap {
    font-size: 34px;
    line-height: 42px;
}

h2, .h2 {
    font-size: 32px;
    line-height: 40px;
}

h3, .h3 {
    font-size: 24px;
    line-height: 32px;
}

h4, .h4 {
    font-size: 18px;
    line-height: 26px;
}

h5, .h5 {
    font-size: 16px;
    line-height: 24px;
}

