/**
 * 1.3 Elements
 * -------------------------------------------------------------------
 */
.e-accordion .row, .e-button .row, .e-tabs .row {
    margin-top: 0;
}

.element-wrap {
    margin-top: 40px;
    padding: 0 15px;
}

.e-heading p {
    margin-bottom: 15px;
}

.e-heading p:last-child {
    margin-bottom: 0;
}

/*----- blockquote -----*/
blockquote {
    padding: 8px 0 8px 15px;
    margin-bottom: 0;
    border-left: 5px solid #ea2e2b;
    position: relative;
    margin-top: 20px;
    color: #666666;
    font-size: 16px;
    line-height: 22px;
    font-style: italic;
    position: relative;
}

.element-wrap blockquote {
    margin-top: 30px;
}

/*----- Text style -----*/
.txt-highlight {
    background: #ea2e2b;
    color: #fff;
}

.txt-highlight-2 {
    background: #333333;
    color: #fff;
}

.txt-color {
    color: #333333;
}

.txt-decoration {
    color: #333333;
    text-decoration: underline;
}

.txt-decoration-2 {
    color: #ea2e2b;
    text-decoration: underline;
}

/*----- Column -----*/
.c-title {
    font-family: "Roboto Condensed", sans-serif;
    font-size: 24px;
    line-height: 30px;
    text-transform: capitalize;
    margin-top: 30px;
    margin-bottom: 10px;
    font-weight: 300;
}

.e-column .element-title {
    margin-bottom: 0;
}

/*----- Bootstrap collapse -----*/
.panel-group {
    margin-bottom: 0px;
}

.panel-group .panel {
    background: none;
    border-radius: 0;
    box-shadow: none;
    border: none;
    margin: 0 0 15px 0 !important;
}

.panel-group .panel .panel-heading {
    position: relative;
    border-radius: 0;
    background: #efefef;
    padding: 0;
}

.panel-group .panel .panel-heading > .panel-title a {
    font-family: "Oswald", sans-serif;
    font-size: 13px;
    line-height: 22px;
    color: #333333;
    display: block;
    padding: 9px 60px 9px 20px;
    text-transform: none;
}

.panel-group .panel .panel-heading > .panel-title a .btn-title {
    text-align: center;
    width: 40px;
    height: 40px;
    line-height: 40px;
    font-size: 10px;
    position: absolute;
    top: 0;
    margin-top: -20px;
    margin-left: -20px;
    display: block;
    background: none;
    cursor: pointer;
    margin: 0;
    left: auto;
    right: 0;
}

.panel-group .panel .panel-heading > .panel-title a .btn-title:before {
    content: "";
    width: 12px;
    height: 2px;
    background: #333333;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -6px;
    margin-top: -1px;
}

.panel-group .panel .panel-heading > .panel-title a .btn-title:after {
    content: "";
    width: 2px;
    height: 12px;
    background: #333333;
    position: absolute;
